<template>
  <div>
    <!---->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href=""></a>&gt;<a href=""></a>&gt;<a href=""></a>
        </h2>
      </div>
    </div>
    <!---->
    <div class="main container">
      <div class="pd_info clearfloat">
        <div class="pdinfo_img">
          <img :src="productPic" alt=""/>
        </div>
        <div class="pdinfo_text">
          <h2>{{product.title1}}</h2>
          <ul>
            <li>{{product.model}}</li>
            <li>{{product.func}}</li>
            <li>{{product.noise}}</li>
            <li>{{product.wind}}</li>
            <li>{{product.applyTo}}</li>
            <li>{{product.size}}</li>
            <li>{{product.level}}</li>
            <!--<li>M8088A</li>-->
            <!--<li></li>-->
            <!--<li>19-60dB</li>-->
            <!--<li>690m/</li>-->
            <!--<li></li>-->
            <!--<li>41-60</li>-->
            <!--<li></li>-->
          </ul>
          <p><span></span><strong id="price">{{product.price}}</strong></p>
          <a @click.prevent="addCart(product.pid)" id="addCart"><span class="icon_cart"></span></a>
        </div>
      </div>
      <div class="details_box">
        <h2 class="title"></h2>
        <div class="pd_details" v-html="product.detail">
          <!--<p>M-8088AM-8088AM-8088AM-8088AM-8088AM-8088AM-8088A</p>-->
          <!--<img src="product-imgs/m8088a/01.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/02.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/03.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/04.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/05.jpg" alt=""/>-->
          <!--<img src="product-imgs/m8088a/06.jpg" alt=""/>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pid:0,
      product:{},
      productPic:''
    }
  },
  mounted(){
    this.pid=this.$route.params.pid;
    let url='http://127.0.0.1:8080/product/detail/'+this.pid;
    this.$http.get(url).then(result=>{
      this.product=result.body;
      this.productPic=require('../assets/'+this.product.pic);
    })
  },
  methods:{
    addCart(pid){
      let uid=sessionStorage.uid;
      let uname=sessionStorage.uname;
      if(!uid||!uname){//
        this.$router.push('/login');
      }else{//ajax
        let url='http://127.0.0.1:8080/cart/detail/add';
        this.$http.post(url,{uid,pid}).then(result=>{
          if(result.body.code==1){
            alert('!');
            this.bus.$emit('getHeaderCartDetails');
          }
        });
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



