<script>

export default {
  data () {
    return {
      nid:0,
      news:{}
    }
  },
  mounted(){
    this.nid=this.$route.params.nid;
    let url='http://127.0.0.1:8080/news/detail/'+this.nid;
    this.$http.get(url).then(result=>{
      this.news=result.body;
      console.log(result.body);
    });
  }
}
</script>

