<template>
  <div class="login_box">
    <div class="container">
      <div class="login">
        <h2></h2>
        <dl class="clearfloat">
          <dt><span class="tips" id="uname_prompt_text">{{unameInfo}}</span></dt>
          <dd>
            <input type="text" id="uname" placeholder="/"
                   v-model.trim="uname" @blur="checkUname"/>
            <em class="icon_user"></em>
            <i class="icon_error"
               :style="{'display':unameInfo==''?'none':'inline-block'}"
               id="uname_prompt_icon"></i>
          </dd>
        </dl>
        <dl>
          <dt><span class="tips" id="pwd_prompt_text">{{upwdInfo}}</span></dt>
          <dd>
            <input type="password" id="pwd" placeholder=""
                   v-model.trim="upwd" @blur="checkUpwd"/>
            <em class="icon_pwd"></em>
            <i class="icon_error"
               :style="{'display':upwdInfo==''?'none':'inline-block'}"
               id="pwd_prompt_icon"></i>
          </dd>
        </dl>
        <p>
          <input type="checkbox" id="auto_login"/>
          <label for="auto_login"><span></span></label>
        </p>
        <button type="button" id="login" @click="toLogin">   </button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        uname:'',
        unameInfo:'',
        unameIsOk:false,
        upwd:'',
        upwdInfo:'',
        upwdIsOk:false
      }
    },
    methods:{
      checkUname(){
        if(!this.uname){
          this.unameInfo='';
        }else{
          this.unameInfo='';
          this.unameIsOk=true;
        }
      },
      checkUpwd(){
        if(!this.upwd){
          this.upwdInfo='';
        }else{
          this.upwdInfo='';
          this.upwdIsOk=true;
        }
      },
      toLogin(){
        if(!(this.unameIsOk&&this.upwdIsOk)){
          this.unameInfo='';
          this.unameIsOk=false;
        }else{
          //ajax
          let url='http://127.0.0.1:8080/user/login?unameOrPhone='+this.uname+'&upwd='+this.upwd;
          this.$http.get(url).then(result=>{
            let data=result.body;
            if(data.code!=1){
              this.unameInfo='';
            }else{
              sessionStorage.uid=data.uid;
              sessionStorage.uname=data.uname;
              this.bus.$emit('headerReload');
              this.$router.go(-1);
            }
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



